import React, { useState, useEffect, useRef } from 'react';
import Quiz from './components/Quiz';
import { TransitionGroup } from 'react-transition-group';
import Result from './components/Result';
import colorArray from './api/color.json';
import './App.css';
import leftArrow from './assets/left.png'
import rightArrow from './assets/right.png'
import Welcome from './components/Welcome';

function App() {

  const [state, setState] = useState({
    stdinfo: {},
    start: false,
    loading: true,
    counter: 0,
    questionId: null,
    question: {},
    answer: null,
    answersCount: {},
    result: false,
    pre: false,
    next: false,
    length: 0,
    submit: false
  })
  const quizQuestions = useRef([]);
  const [lang, setLang] = useState({
    flag: true,
    lg: 'NP'
  })
  const getData = async () => {
    return fetch('./quizQuestions.json', {
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }).then(function (response) {
      return response.json();
    })
  }
  const changeLang = () => {
    setLang(pre => {
      if (pre.flag) {
        return {
          flag: false,
          lg: 'EN'
        }
      }
      return {
        flag: true,
        lg: 'NP'
      }
    })
  }
  useEffect(async () => {
    quizQuestions.current = await getData()
    let count = 0;
    let ans = {}
    let cans = null;
    let start = false

    const qcount = localStorage.getItem("qsn")
    console.log(localStorage.getItem("qsn"))
    if (qcount != null && qcount != "") {
      count = parseInt(qcount)
      ans = JSON.parse(localStorage.getItem("ans"))
      const qid = quizQuestions.current[count].id
      cans = ans[qid]
      start = true;
    }
    setState({
      ...state,
      start: start,
      counter: count,
      current: count,
      loading: false,
      answersCount: ans,
      answer: cans,
      questionId: quizQuestions.current[count].id,
      question: quizQuestions.current[count],
      length: quizQuestions.current.length,
    });
  }, [])

  async function resetAll() {
    quizQuestions.current = await getData()
    localStorage.removeItem("ans")
    localStorage.removeItem("qsn")
    localStorage.removeItem("info")
    let count = 0;
    setState({
      start: false,
      counter: count,
      current: count,
      loading: false,
      answer: null,
      answersCount: {},
      result: false,
      pre: false,
      next: false,
      length: 0,
      submit: false,
      questionId: quizQuestions.current[count].id,
      question: quizQuestions.current[count],
      length: quizQuestions.current.length,
      stdinfo: {},
    });
    // window.location.reload()
  }

  // function 
  function handleAnswerSelected(event) {
    setUserAnswer(event.currentTarget.value);
    if (state.current < (state.length - 1)) {
      setTimeout(() => setNextQuestion(), 300);
    } else {
      setTimeout(() => setState({ ...state, result: true }), 300);
    }
  }
  function moveLeft() {
    if (state.current == 0) {
      return false
    }
    setState((state, props) => {
      const current = state.current - 1
      const qid = quizQuestions.current[current].id
      return {
        ...state,
        current: current,
        questionId: qid,
        question: quizQuestions.current[current],
        answer: state.answersCount[qid],
        next: state.counter > current,
        pre: current > 0
      }
    });
  }

  function moveRight() {
    if (state.current == state.counter) {
      return false
    }
    setState((state, props) => {
      const current = state.current + 1
      const qid = quizQuestions.current[current].id
      return {
        ...state,
        current: current,
        questionId: qid,
        question: quizQuestions.current[current],
        answer: state.answersCount[qid],
        next: state.counter > current,
        pre: current > 0
      }
    });
  }
  function goBack() {
    setState({ ...state, result: false })
  }
  function onStart(info) {
    localStorage.setItem("info", JSON.stringify(info))
    setState({ ...state, stdinfo: info, start: true })
  }
  async function onSubmit() {
    // const url = document.querySelector("#pjaxbillingModal").getAttribute("data-url");
    const url = document.querySelector("#questionContainer").getAttribute("data-url") + "/api/feeds";
    // url += "feeds"
    const sinfo = localStorage.getItem("info")
    const ans = localStorage.getItem("ans")
    const answers = { ...(JSON.parse(sinfo)), ans: ans }
    console.log(answers)
    await fetch(url,
      {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        mode: 'cors',
        cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
        headers: {
          'Content-Type': 'application/json'
        },
        referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
        body: JSON.stringify(answers) // body data type must match "Content-Type" header}
      }
    ).then(e => {
      console.log(e)
      if (e.ok) {
        localStorage.removeItem("ans")
        localStorage.removeItem("qsn")
        localStorage.removeItem("info")
        setState(pre => ({
          ...pre,
          submit: true
        }))
      }
    })
  }
  function setUserAnswer(answer) {
    setState((pre, props) => {
      const ans = {
        ...(pre.answersCount),
        [pre.questionId]: answer
      }
      localStorage.setItem("ans", JSON.stringify(ans))
      localStorage.setItem("qsn", pre.current)
      return {
        ...pre,
        answersCount: ans,
        answer: answer
      }
    });
  }
  function setNextQuestion() {
    setState((pre => {
      const current = pre.current + 1;
      const counter = pre.counter == pre.current ? current : pre.counter;
      const qsn = quizQuestions.current[current]
      return {
        ...pre,
        counter,
        current: current,
        questionId: qsn.id,
        question: qsn,
        answer: pre.answersCount[qsn.id],
        next: pre.counter > pre.current,
        pre: current > 0
      }
    }));
  }

  function renderQuiz() {
    return (
      <div style={{ display: 'flex' }}>
        <div className="arrowButton">
          <div className="arbutton" style={{ display: state.pre ? 'block' : 'none' }} onClick={moveLeft}  >
            <img src={leftArrow} />
          </div>
        </div>
        <Quiz
          lang={lang}
          onChangeLang={changeLang}
          onReset={resetAll}
          answer={state.answer}
          question={state.question}
          questionTotal={state.length}
          onAnswerSelected={handleAnswerSelected}
        />
        <div className="arrowButton">
          <div className="arbutton right" style={{ display: state.next ? 'block' : 'none' }} onClick={moveRight} >
            <img src={rightArrow} />
          </div>
        </div>
      </div>
    );
  }

  function renderResult() {
    return <Result onGoBack={goBack} onSubmit={onSubmit} submited={state.submit} />;
  }
  if (!state.start) {
    return <Welcome onStart={onStart} />
  }

  return (
    <div>
      <div style={{
        backgroundColor: '#222',
        padding: '20px',
        color: 'white',
        textAlign: 'center'
      }}>
        <h2>Student's questionnaires</h2>
      </div>
      <TransitionGroup
        className="qsnContainer"
        style={(state.result ? { backgroundColor: "#30ff50bf" } : { backgroundColor: colorArray[state.current] })}
      >
        {state.loading ? (
          <p>Loading ...</p>
        ) : (state.result ? renderResult() : renderQuiz())}
      </TransitionGroup>
    </div >
  );
}

export default App;
