import React from 'react';
import PropTypes from 'prop-types';
import { CSSTransition } from 'react-transition-group';
import Question from '../components/Question';
import QuestionCount from '../components/QuestionCount';
import AnswerOption from '../components/AnswerOption';


function Quiz({ question, answer, lang, onAnswerSelected, questionTotal, onChangeLang, onReset }) {
  function renderAnswerOptions(ans, id) {
    return (
      <AnswerOption
        key={id}
        answerType={id}
        answerContent={ans}
        answer={answer}
        questionId={question.id}
        onAnswerSelected={onAnswerSelected}
      />
    );
  }
  return (
    <CSSTransition
      timeout={500}
      style={{ width: '100%', padding: '5px' }}
    >
      <div key={question.id}  >
        <QuestionCount counter={question.id} total={questionTotal} lang={lang} onChangeLang={onChangeLang} onReset={onReset} />
        <Question content={lang.flag ? question.qsn2 : question.qsn} />
        <ul className="answerOptions">
          {lang.flag ? question.options2.map(renderAnswerOptions) : question.options.map(renderAnswerOptions)}
        </ul>
      </div>
    </CSSTransition>
  );
}

Quiz.propTypes = {
  answer: PropTypes.string.isRequired,
  question: PropTypes.string.isRequired,
  questionTotal: PropTypes.number.isRequired,
  onAnswerSelected: PropTypes.func.isRequired,
  onReset: PropTypes.func.isRequired
};

export default Quiz;
