import React from 'react';
import PropTypes from 'prop-types';
import { CSSTransition } from 'react-transition-group';

function Result({ onSubmit, onGoBack, submited }) {
  return (
    <CSSTransition
      timeout={500}
    >
      <div style={{ padding: '20px' }}>
        <div>
          <h3>Thank you for particapting in the questionnaires.</h3>
        </div>
        {submited ? (<p>Submited Successfully</p>) : (
          <div style={{ padding: '20px 0' }}>
            <button style={{ marginRight: '20px' }} className="btn btn-success" type="button" onClick={onSubmit}>Submit</button>
            <button className="btn btn-info" type="button" onClick={onGoBack}>Back</button>
          </div>
        )}
      </div>
    </CSSTransition>
  );
}

Result.propTypes = {
  onGoBack: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
};

export default Result;
