import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from 'yup';
import { useForm, Controller } from "react-hook-form";
import Info from './Info';

const schema = yup.object({
  name: yup.string().required("Name cannot be blank"),
  classno: yup.string().required("Class cannot be blank"),
  rollno: yup.string().required("Rollno cannot be blank"),
  section: yup.string().required("Section cannot be blank"),
  gender: yup.string().required("Gender cannot be blank"),
  age: yup.string().required("Age cannot be blank"),
});
function Welcome({ onStart }) {
  const [start, setStart] = useState(false)
  const { handleSubmit, control, formState: { errors, isSubmitting } } = useForm({
    resolver: yupResolver(schema),
  });
  if (!start) {
    return <Info setStart={setStart} />
  }
  return (
    <div className="qsnContainer" style={{ padding: '10px' }}>
      <p><b>Student's general information</b></p>
      <form noValidate onSubmit={handleSubmit(onStart)}>
        <div className='mcq-form'>
          <Controller
            name="name"
            defaultValue=""
            control={control}
            render={({ field, fieldState }) => {
              return (
                <>
                  <label className='mcq-label'>Full Name</label>
                  <input
                    type="text"
                    className='mcq-input'
                    placeholder="Enter your full name"
                    {...field}
                  />
                  {fieldState.error && (
                    <p className="mcq-error" style={{ color: 'red' }}>
                      {fieldState.error?.message}
                    </p>
                  )}
                </>
              )
            }}
          />
          <Controller
            name="classno"
            defaultValue=""
            control={control}
            render={({ field, fieldState }) => {
              return (
                <>
                  <label className='mcq-label'>Class</label>
                  <div className='mcq-input' style={{ display: "flex", gap: 26 }}>
                    <label><input type='radio' {...field} value={9} /> Nine</label>
                    <label><input type='radio'  {...field} value={10} /> Ten</label>
                    <label><input type='radio'  {...field} value={11} /> Eleven</label>
                    <label><input type='radio' {...field} value={12} /> Twelve</label>
                  </div>
                  {fieldState.error && (
                    <p className="mcq-error" style={{ color: 'red' }}>
                      {fieldState.error?.message}
                    </p>
                  )}
                </>
              )
            }}
          />
          <Controller
            name="rollno"
            defaultValue=""
            control={control}
            render={({ field, fieldState }) => {
              return (
                <>
                  <label className='mcq-label'>Roll No</label>
                  <input
                    type="text"
                    className='mcq-input'
                    placeholder="Enter your rollno"
                    {...field}
                  />
                  {fieldState.error && (
                    <p className="mcq-error" style={{ color: 'red' }}>
                      {fieldState.error?.message}
                    </p>
                  )}
                </>
              )
            }}
          />
          <Controller
            name="section"
            defaultValue=""
            control={control}
            render={({ field, fieldState }) => {
              return (
                <>
                  <label className='mcq-label'>Section</label>
                  <input
                    type="text"
                    className='mcq-input'
                    placeholder="Enter your section"
                    {...field}
                  />
                  {fieldState.error && (
                    <p className="mcq-error" style={{ color: 'red' }}>
                      {fieldState.error?.message}
                    </p>
                  )}
                </>
              )
            }}
          />
          <Controller
            name="gender"
            defaultValue=""
            control={control}
            render={({ field, fieldState }) => {
              return (
                <>
                  <label className='mcq-label'>Gender</label>
                  <div className='mcq-input' style={{ display: "flex", gap: 26 }}>
                    <label><input type='radio' {...field} value={"M"} /> Male</label>
                    <label><input type='radio'  {...field} value={"F"} /> Gemale</label>
                  </div>
                  {fieldState.error && (
                    <p className="mcq-error" style={{ color: 'red' }}>
                      {fieldState.error?.message}
                    </p>
                  )}
                </>
              )
            }}
          />
          <Controller
            name="age"
            defaultValue=""
            control={control}
            render={({ field, fieldState }) => {
              return (
                <>
                  <label className='mcq-label'>Age</label>
                  <input
                    type="number"

                    className='mcq-input'
                    placeholder="Enter your age"
                    min={10}
                    max={21}
                    {...field}
                  />
                  {fieldState.error && (
                    <p className="mcq-error" style={{ color: 'red' }}>
                      {fieldState.error?.message}
                    </p>
                  )}
                </>
              )
            }}
          />
          <button style={{ marginTop: '20px' }} className="btn btn-success mcq-button" type="submit">Start</button>
        </div>
      </form>
    </div>
  );
}

Welcome.propTypes = {
  onStart: PropTypes.func.isRequired,
};

export default Welcome;
