import React from 'react';
import PropTypes from 'prop-types';

function QuestionCount(props) {
  return (
    <div className="questionHeader">
      <div className="questionCount">
        Question <span>{props.counter}</span> of <span>{props.total}</span>
      </div>
      <div>
        <button onClick={() => {
          var result = confirm("Are you sure you want to reset this session?");
          if (!result) {
            return false;
          }
          return props.onReset()
        }} className="btn btn-sm btn-warning" style={{ marginRight: 20, borderRadius: 20 }} type='button'>Reset</button>
        Language&nbsp;:&nbsp;
        <span>{props.lang.lg}</span>
        <label className="switch">
          <input type="checkbox" onChange={props.onChangeLang} checked={props.lang.flag} />
          <span className="slider round"></span>
        </label>
      </div>
    </div>
  );
}

QuestionCount.propTypes = {
  counter: PropTypes.number.isRequired,
  total: PropTypes.number.isRequired,
  onChangeLang: PropTypes.func.isRequired,
  lang: PropTypes.string.isRequired
};

export default QuestionCount;
